<template>
  <div class="w-100 order flex-col">
    <div class="p-15" style="background:rgba(229,246,247,1);">
      <el-row :gutter="10">
        <el-form :inline="true" class="demo-form-inline">
          <el-col>
            <el-form-item label="" class="m-b-0">
              <el-input
                v-model="search.search"
                placeholder=""
                style="width:100%;"
              ></el-input>
            </el-form-item>
            <el-form-item label="" class="m-b-0">
              <el-input
                v-model="search.model"
                placeholder=""
                style="width:100%;"
              ></el-input>
            </el-form-item>
            <el-form-item label="" class="m-b-0">
              <el-input
                v-model="search.partNo"
                placeholder=""
                style="width:100%;"
              ></el-input>
            </el-form-item>
            <el-button
              type="success"
              icon="el-icon-search"
              @click="getList()"
              ></el-button>
            <el-button type="success"	icon="el-icon-plus" @click="addDig"></el-button>

            <el-form-item label="" class="m-b-0 right m-r-20">
                <div class="left c-p c-9">
                  <el-upload
                    class="upload-demo"
                    action=""
                    :on-change="upload"
                    :show-file-list="false"
                    :auto-upload="false"
                    accept="application/vnd.openxmlformats-officedocument.spreadsheetml.sheet,application/vnd.ms-excel">
                    <el-button size="small" type="success">excel</el-button>
                  </el-upload>
                </div>
            </el-form-item>
          </el-col>
        </el-form>
      </el-row>
    </div>
    <div style="width:100%;padding-top:15px;" class="orderTable" id="printBox">
      <el-table :data="tableData" :max-height="maxHeight" :height="500" style="width: 100%">
        <el-table-column type="index" label=""> </el-table-column>
        <el-table-column prop="partName" label="" width="150"></el-table-column>
        <el-table-column prop="model" label="" width="150"> </el-table-column>
        <el-table-column prop="unit" label=""> </el-table-column>
        <el-table-column prop="exceeds" label=""> </el-table-column>
        <el-table-column prop="sellPrice" label=""> </el-table-column>
        <el-table-column prop="total" label=""></el-table-column>
        <el-table-column prop="upperLimit" label=""></el-table-column>
        <el-table-column prop="lowerLimit" label=""> </el-table-column>
        <el-table-column prop="partNo" label=""> </el-table-column>
        <el-table-column prop="employeeName" label="" width="100" align="center">
          <template slot-scope="scope">
            <el-dropdown @command="handleCommand" trigger="click">
              <span class="el-dropdown-link">
                <i class="el-icon-arrow-down el-icon--right"></i>
              </span>
              <el-dropdown-menu slot="dropdown">
                <el-dropdown-item :command="beforeHandleCommand('a',scope.row)"></el-dropdown-item>
                <el-dropdown-item :command="beforeHandleCommand('b',scope.row)"></el-dropdown-item>
                <el-dropdown-item :command="beforeHandleCommand('c',scope.row)"></el-dropdown-item>
                <el-dropdown-item :command="beforeHandleCommand('d',scope.row)"></el-dropdown-item>
                <el-dropdown-item :command="beforeHandleCommand('e',scope.row)"></el-dropdown-item>
              </el-dropdown-menu>
            </el-dropdown>
          </template>
        </el-table-column>
      </el-table>
      <div class="p-tb-10 text-center">
        <el-pagination
          :hide-on-single-page="pageLength === 0"
          background
          layout="prev, pager, next"
          :page-size="size"
          @current-change="handleCurrentChange"
          :total="pageLength"
        >
        </el-pagination>
      </div>
    </div>

    <el-dialog :title="title" :visible.sync="editStatus" center width="30%" :modal="false">
      <el-form :model="editPartsForm">
        <el-form-item label="" label-width="100px">
          <el-input v-model="editPartsForm.partName" :disabled="title === ''"></el-input>
        </el-form-item>
        <el-form-item label="" label-width="100px">
          <el-input v-model="editPartsForm.model"  :disabled="title === ''"></el-input>
        </el-form-item>
        <el-form-item label="" label-width="100px">
          <el-input v-model="editPartsForm.fitCars"  :disabled="title === ''"></el-input>
        </el-form-item>
        <el-form-item label="" label-width="100px">
          <el-input v-model="editPartsForm.unit"  :disabled="title === ''"></el-input>
        </el-form-item>
        <el-form-item label="" label-width="100px">
          <el-input v-model="editPartsForm.exceeds"  :disabled="title === ''"></el-input>
        </el-form-item>
        <el-form-item label="" label-width="100px">
          <el-input v-model="editPartsForm.sellPrice"></el-input>
        </el-form-item>
        <el-form-item label="" label-width="100px">
          <el-input v-model="editPartsForm.upperLimit"></el-input>
        </el-form-item>
        <el-form-item label="" label-width="100px">
          <el-input v-model="editPartsForm.lowerLimit"></el-input>
        </el-form-item>
        <el-form-item label="" label-width="100px">
          <el-input v-model="editPartsForm.partNo"  :disabled="title === ''"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button type="danger" @click="editStatus = false"> </el-button>
        <el-button type="success" @click="editFormSubmit"> </el-button>
      </div>
    </el-dialog>

    <el-dialog :title="title1" :visible.sync="storeStatus" center width="30%" :modal="false">
      <el-form :model="storeForm">
        <el-form-item label="" label-width="100px">
          {{storeForm.partName}}
        </el-form-item>
        <el-form-item label="" label-width="100px">
          {{storeForm.model}}
        </el-form-item>
        <el-form-item label="" label-width="100px" v-if="title1 === ''">
          <el-input v-model="storeForm.count"></el-input>
        </el-form-item>
          <el-form-item label="" label-width="100px" v-if="title1 === ''">
          <el-input v-model="storeForm.count"></el-input>
        </el-form-item>
        <el-form-item label="" label-width="100px">
          <el-input v-model="storeForm.remark"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button type="danger" @click="storeStatus = false"> </el-button>
        <el-button type="success" @click="storeFormSubmit"> </el-button>
      </div>
    </el-dialog>

    <el-dialog title="" :visible.sync="historyStatus" width="60%" center :modal="false">
      <el-form
          :inline="true"
          class="demo-form-inline"
        >
        <el-row>
          <el-col>
            <el-form-item label="" class="m-b-0">
              {{historyInfo.partName}}
            </el-form-item>
            <el-form-item label="" class="m-b-0">
              {{historyInfo.model}}
            </el-form-item>
            <el-form-item label="" class="m-b-0">
              {{historyInfo.supplierName}}
            </el-form-item>
            <el-form-item label="" class="m-b-0">
              {{historyInfo.partNo}}
            </el-form-item> 
          </el-col>
        </el-row>
        <el-row>
          <el-col>
            <el-form-item label="" class="m-b-0">
              <el-date-picker
                v-model="searchTime"
                type="daterange"
                range-separator="-"
                start-placeholder=""
                end-placeholder=""
                unlink-panels
                :picker-options="pickerOptions"
              >
              </el-date-picker>
            </el-form-item>
            <el-form-item
              label=""
              class="m-b-0"
            >
              <el-checkbox-group v-model="searchType">
                <el-checkbox label="0"></el-checkbox>
                <el-checkbox label="1"></el-checkbox>
              </el-checkbox-group>
            </el-form-item>
            <el-button
              type="success"
              icon="el-icon-search"
              @click="getHistory"
            ></el-button>
            <el-form-item
              label=""
              class="m-b-0 right m-r-20"
            >
            </el-form-item>
          </el-col>
        </el-row>
      </el-form>
      <el-table :data="history">
        <el-table-column prop="createdDate" label=""></el-table-column>
        <el-table-column prop="model" label="">
          <template slot-scope="scope">
            {{scope.row.type == 0 ? '' : ''}}
          </template>
        </el-table-column>
        <el-table-column prop="count" label=""> </el-table-column>
        <el-table-column prop="carNo" label=""> </el-table-column>
        <el-table-column prop="billNumber" label=""></el-table-column>
        <el-table-column prop="remark" label=""></el-table-column>
      </el-table>
      <div slot="footer" class="dialog-footer">
        <el-button @click="historyStatus = false"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { getPart, addPart, updatePart, saveRecord, deletePart, pageRecords } from "../../request/api.js"
import moment from 'moment'
export default {
  data() {
    return {
      pickerOptions: {
        onPick: ({ maxDate, minDate }) => {
          this.selectDate = minDate.getTime();
          if (maxDate) {
            this.selectDate = "";
          }
        },
        disabledDate: time => {
          if (this.selectDate !== "") {
            const one = 30 * 24 * 3600 * 1000;
            const minTime = this.selectDate - one;
            const maxTime = this.selectDate + one;
            return time.getTime() < minTime || time.getTime() > maxTime;
          }
        },
        shortcuts: [{
            text: '',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              picker.$emit('pick', [start, end]);
            }
          }, {
            text: '',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * (Number(moment(new Date()).format("D")) - 1));
              picker.$emit('pick', [start, end]);
            }
          }]
      },
      search: {
        garageId: localStorage.getItem('gid'),
        search: "",
        model: '',
        partNo: ''
      },
      tableData: [],
      maxHeight: undefined,
      pageLength: null,
      size: 10,
      page: 0,
      title: '',
      editStatus: false,
      editPartsForm: {},
      title1: '',
      storeStatus: false,
      storeForm: {},
      supplier: [],
      historyStatus: false,
      history: [],
      searchTime: [
        moment(new Date()).format("YYYY-MM-DD"),
        moment(new Date()).format("YYYY-MM-DD")
      ],
      searchType: [],
      historySearch: {},
      historyInfo: {}
    };
  },
  created() {
    this.getList()
  },
  mounted() {
    this.maxHeight = $(".tabBox").height() - 235 + "px";
  },
  methods: {
    handleCurrentChange(val) {
      this.page = val - 1;
      this.getList()
    },
    getList() {
      const data = {
        page: this.page,
        size: this.size,
        ...this.search
      }
      getPart(data).then(res => {
        this.pageLength = res.data.pageInfo.total
        this.tableData = res.data.data
      })
    },
    editFormSubmit() {
              console.log(this.editPartsForm)
      if(this.title === ''){
        addPart(this.editPartsForm).then(res => {
          if(res.data.code == 200){
            this.$message({
              type: 'success',
              message: '!'
            })
            this.getList()
            this.editStatus = false
          }
        })
      }else{
        console.log(this.editPartsForm)
        updatePart(this.editPartsForm).then(res => {
          if(res.data.code == 200){
            this.$message({
              type: 'success',
              message: '!'
            })
            this.getList()
            this.editStatus = false
          }
        })
      }
    },
    storeFormSubmit() {
      let type = ''
      if(this.title1 === ''){
        type = 1
      }else{
        type = 0
      }
      
      this.storeForm.type = type

      saveRecord(this.storeForm).then(res => {
        if(res.data.code == 200){
          this.$message({
            type: 'success',
            message: '!'
          })
          this.getList()
          this.storeStatus = false
        }
      })
    },
    upload(file,fileList){
        let files = {0:file.raw}
        this.readExcel1(files);
    },
    async readExcel1(files) {//
        var that = this;
        if(files.length<=0){//
            return false;
        }else if(!/\.(xls|xlsx)$/.test(files[0].name.toLowerCase())){
            this.$Message.error('xlsxlsx');
            return false;
        }

        const fileReader = new FileReader();
        fileReader.onload = async (ev) => {
            try {
                const data = ev.target.result;
                const workbook = XLSX.read(data, {
                    type: 'binary'
                });
                const wsname = workbook.SheetNames[0];//
                const ws = XLSX.utils.sheet_to_json(workbook.Sheets[wsname]);//json
                console.log(ws, 1);
                let addList = ws.map(v => {
                  return {
                    IsFactioryPartName: 0,
                    gid: localStorage.getItem('gid'),
                    partNo: v[''],
                    partName: v[''],
                    unit: v[''],
                    model: v[''],
                    fitCars: v[''],
                    total: v[''],
                    exceeds: v[''],
                    sellPrice: v[''],
                    upperLimit: v[''],
                    lowerLimit: v[''],
                  }
                })
                const loading = this.$loading({
                  lock: true,
                  text: '',
                  spinner: 'el-icon-loading',
                  background: 'rgba(0, 0, 0, 0.7)'
                });
                for (const i in addList) {
                  await addPart(addList[i])
                }
                loading.close()
                this.getList()
                this.$message({
                  type: 'success',
                  message: ''
                })
                try{
                    
                }catch(err){
                    console.log(err)
                }
                
                this.$refs.upload.value = '';
    
            } catch (e) {
    
                return false;
            }
        };
        fileReader.readAsBinaryString(files[0]);
    },
    addDig() {
      this.editPartsForm = {
        IsFactioryPartName: 0,
        gid: localStorage.getItem('gid')
      }
      this.title = ''
      this.editStatus = true
    },
    editDig(row) {
      this.editPartsForm = {
        IsFactioryPartName: 0,
        gid: localStorage.getItem('gid'),
        ...row
      }
      this.title = ''
      this.editStatus = true
    },
    storeDig(row, title) {
      this.storeForm = row
      this.title1 = title
      this.storeStatus = true
    },
    deleteStock(row) {
      if(row.total > 0) return this.$message({type: 'error',message: '0'})
      this.$confirm(`${row.partName}?`, '', {
					confirmButtonText: '',
					cancelButtonText: '',
					type: 'warning'
				}).then(() => {
					deletePart({id: row.id}).then(res => {
						const result = res.data
						if(result.code == 200){
							this.$message({
								type: 'success',
								message: ''
							})
							this.getList()
						}
					})
				}).catch(() => {})
    },
    handleCommand(command){
      switch (command.command) {
        case "a":
        this.editDig(command.row)
        break;
        case "b":
        this.deleteStock(command.row);
        break;
        case "c":
        this.storeDig(command.row, '');
          break;
        case "d":
        this.storeDig(command.row, '');
          break;
        case "e":
        this.historyDig(command.row);
          break;
      }
    },
    beforeHandleCommand(item,row){
      return {
        'command': item,
        'row': row
      }
    },
    historyDig(row){
      this.historyStatus = true
      this.historyInfo = row
      this.history = []
      this.getHistory()
    },
    getHistory(){
      const data = {
        partId: this.historyInfo.id,
        endDate: moment(this.searchTime[1]).format("YYYY-MM-DD"),
        beginDate: moment(this.searchTime[0]).format("YYYY-MM-DD"),
        page: 0,
        size: 1000
      }
      if(this.searchType.length > 0) data.type = this.searchType.join(',')
      pageRecords(data).then(res => {
        this.history = res.data.data
      })
    }
  }
}
</script>
<style scoped>
.navActive em:hover {
		color: #3AC29F;
		transition: .5s;
	}
	
	.navActive em.active {
		color: #3AC29F;
	}
</style>
<style>
.el-loading-spinner .el-loading-text {
  text-align: center;
}
</style>



