<template>
 <div class="w-100 order flex-col h-100" style="overflow-x:hidden;">
    <el-row :gutter="20">
        <el-col :span="24" style="text-align:center;padding: 50px;">
          <el-image :src="qrcode" style="width:300px;height:300px;"></el-image>
        </el-col>
        <el-col :span="24" style="text-align:center;">
          <el-button type="success" @click="downhander"></el-button>
        </el-col>
    </el-row>
</div>
</template>

<script>
import { getQrCode } from "../../request/api.js";

export default {
  data() {
    return {
      qrcode: ''
    }
  },
  created() {
    getQrCode({gid: localStorage.getItem("gid")}).then(e => {
      this.qrcode = e.data.data.qrCodePic
    })
  },
  methods: {
    downhander(){
      this.downloadImg(this.qrcode,'pic')
      this.$message({
        message: '-',
        type: 'warning'
      })
    },
    downloadImg(imgsrc, name){
      var image = new Image();
      //  Canvas 
      image.setAttribute("crossOrigin", "anonymous");
      image.onload = function() {
        var canvas = document.createElement("canvas");
        canvas.width = image.width;
        canvas.height = image.height;
        var context = canvas.getContext("2d");
        context.drawImage(image, 0, 0, image.width, image.height);
        var url = canvas.toDataURL(); //base64
        console.log(url)

        var a = document.createElement("a"); // a
        var event = new MouseEvent("click"); // 
        a.download = name || "photo"; // 
        a.href = url; // URLa.href
        a.dispatchEvent(event); // a
      };
      image.src = imgsrc;
    }
  }
};
</script>

<style scoped>
</style>


