<style>
textarea{width:100%;height:420px;font:12px/1.6em "Lucida Console","monospace","Consolas";letter-spacing:1px}
textarea::-webkit-scrollbar{width:10px;height:10px}
textarea::-webkit-scrollbar-button:vertical:increment{background-color:transparent}
textarea::-webkit-scrollbar-track:enabled{background-color:rgba(113,112,107,0.1);-webkit-border-radius:5px}
textarea::-webkit-scrollbar-thumb:vertical{height:50px;background-color:rgba(0,0,0,0.2);-webkit-border-radius:5px}
textarea::-webkit-scrollbar-thumb:horizontal{width:50px;background-color:rgba(0,0,0,0.2);-webkit-border-radius:5px}
</style>
<template>
	<center>
		<textarea id="code" spellcheck="false"></textarea>
		<br>
		<input type="button" name="" value="" id="encode">
		<input type="button" name="" value="" id="decode">
		<input type="button" name="" value="" id="purify">
		<input type="button" name="" value="" id="empty">
	</center>
</template>

<script>
export default {
  name: "CssClear",
  data() {
    return {};
  },
  mounted() {
    var oEncodeBtn = document.getElementById("encode");
    var oDecodeBtn = document.getElementById("decode");
    var oPurify = document.getElementById("purify");
    var oCode = document.getElementById("code");
    var oEmpty = document.getElementById("empty");
    oEmpty.onclick = function() {
      oCode.value = "";
    };

    oEncodeBtn.onclick = function() {
      var val = oCode.value;
      val = val.replace(/\/\*(.|\n)*?\*\//g, ""); //
      val = val.replace(/^\s+|\s+$/g, ""); //
      val = val.replace(/(:)\s+/g, "$1"); //                width:  100px  => width:100px
      val = val.replace(/\s{2,}/g, " "); // 2              margin: 10px   20px  30px  => margin:10px 20px 30px
      val = val.replace(/,\s+|\s+,/g, ","); //      h1,  h2  , h3  =>h1,h2,h3
      val = val.replace(/;{2,}|;\s+/g, ";"); //    width:200px;;  height:100px  => width:200px;height:100px
      val = val.replace(/\s*\{\s*/g, "{"); //            div { height:100px}   => div{height:100px}
      val = val.replace(/\s*}\s*/g, "}"); //            div{height:100px}  a{}   => div{height:100px}a{}
      val = val.replace(/[\n\t\f\r]/g, ""); //,,,
      val = val.replace(/;}/g, "}"); //            div{height:100px;} => div{height:100px}
      /*  */
      oCode.value = val;
    };

    oPurify.onclick = function() {
      //;
      oEncodeBtn.click();
      var val = oCode.value;
      val = val.replace(/\}/g, "}\n");
      oCode.value = val;
    };

    oDecodeBtn.onclick = function() {
      //
      var val = oCode.value;
      val = val.replace(/(\*\/)\s+(\w)/g, "$1\n$2"); //
      val = val.replace(/(:)\s+/g, "$1"); //
      val = val.replace(/,\s+|\s+,/g, ","); //
      val = val.replace(/;{2,}|;\s+/g, ";"); //
      val = val.replace(/;}/g, "}"); //
      val = val.replace(/\s*\{\s*/g, " {\n\t"); //{
      val = val.replace(/\s*}\s*/g, "\n}\n"); //}
      val = val.replace(/\s*;\s*/g, ";\n\t"); //
      oCode.value = val;
    };
  }
};
</script>





